@import 'variables.scss';

.wrapper {
    .content {
        gap: 0.25em;

        .title {
            position: relative;
            font-size: 22px;
        }
    }

    .item {
        height: 50px;
        padding-left: 16px;
        padding-right: 22px;
        background-color: #1e2033;
        
        a {
            display: flex;
            align-items: center;
            gap: 10px;
            font-size: 18px;
            line-height: 27px;
            color: #ebecff;
            white-space: nowrap;
            font-weight: 400;
        }

        &:hover {
            background-color: #24263d;
            cursor: pointer;
        }
    }

    &:not(.viewed) {
        .title {
            &::after {
                content: "";
                position: absolute;
                top: -4px;
                right: -12px;
                height: 8px;
                width: 8px;
                border-radius: 100%;
                background-color: $primary-accent-color;
            }
        }
    }
}